*,
*::before,
*::after {
    margin: 0;
    box-sizing: border-box;
}

@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap');

.App {
    width: 100vw;
    min-height: 100vh;
    font-family: 'Roboto', sans-serif;
    background: linear-gradient(-45deg, green, blue, purple, red, yellow);
    background-size: 400%;
    animation: background 45s ease infinite;
}

.hero {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.hero__img {
    border-radius: 50%;
    margin: 2rem;
}

.hero__title {
    color: white;
}

.hero__greetings {
    width: 40%;
    padding: 1rem 2.5rem;
    background-color: lightblue;
    border-radius: 0.75rem;
    margin: 3rem;
}

.buttons__div {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 2rem;
    cursor: pointer;
}

.button__link {
    width: 40%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    gap: 3rem;
    border-radius: 0.75rem;
    background-color: antiquewhite;
    padding: 0.5rem 1rem;
    text-align: center;
}

.button__img {
    width: 3rem;
    height: auto;
    border-radius: 0.5rem;
}

.button__p {
    // color: white;
    & span {
        font-weight: 700;
        text-transform: uppercase;
    }
}

@keyframes background {
    0% {
        background-position: 0% 50%;
    }
    50% {
        background-position: 100% 50%;
    }
    100% {
        background-position: 1% 50%;
    }
}